@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.loading-container {
  width: 100%;
}
.loading-container .ant-spin-spinning {
  width: 100%;
}
.ContentView {
  width: 100%;
  margin: 0 auto;
}
.slogan {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.slogan :nth-child(1) {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 36px;
  color: #000000;
  letter-spacing: 1.12px;
  text-align: center;
  font-weight: 500;
  line-height: 50px;
  margin-top: 28px;
  white-space: pre-line;
}
.decorationImage {
  position: absolute;
  top: -39px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/leaf.png') bottom no-repeat;
  width: 39px;
  height: 39px;
  background-size: contain;
}
@media screen and (max-width: 1080px) {
  .slogan :nth-child(1) {
    font-size: 30px;
  }
}
@media screen and (max-width: 524px) {
  .slogan-view {
    display: none;
  }
  .slogan :nth-child(1) {
    display: none;
  }
}
